  border-radius: 0px 0px 50px 50px;
  margin-bottom: 10px;
}

.contenedor {
  display: grid;
  justify-content: center;
  margin: auto;
  grid-template-columns: repeat(2, 25%);
  grid-column-gap: 10px;
  margin-top: 5px;
}

.contenedor-hijo {
  display: flex;
  align-items: center;
  color: #152938;
  box-shadow: 2px 2px 8px 4px #152938;
  padding: 15px;
  padding-bottom: 35px;
  margin: 10px;
  flex-direction: column;
  background-color: #2471a9;
}

.contenedor-hijo:nth-child(1) {
  margin-top: 50px;
  border-radius: 50px 0px;
}

.contenedor-hijo:nth-child(2) {
  border-radius: 0px 50px;
  margin-top: 50px;
}

.contenedor-hijo:nth-child(3) {
  margin-bottom: 50px;
  border-radius: 0px 50px;
}

.contenedor-hijo:nth-child(4) {
  border-radius: 50px 0px;
  margin-bottom: 50px;
}

